import React from 'react';
import styled from "styled-components";
import Pop from './pop';
import Poptext from './Poptext';
import Poph2 from './Poph2';
import {  Link  } from "react-router-dom";

function PageSkills() {
    return(
        <div className="Home">
            <p>Skills</p>
            <Pop>{'Skills'}</Pop>
            <Poptext>{''}<SLink to="/PageWorks">Works</SLink>{''}</Poptext>
            <Poph2>{'({})'}</Poph2>
            <Poptext>
            {'JavaScript{React,express,babylon}'}<br />
            {'C'}<br />
            {'Python{django}'}<br />
            {'Java'}<br />
            {'HTML,CSS'}<br /><br />
            {'React,express,djangowebBabylonWebGLCPython'}<br /></Poptext><br />
            <Poph2>{''}</Poph2><Poptext>{'Studio One'}<br />{''}</Poptext><br />
            <Poph2>{'CG'}</Poph2><Poptext>{'BlenderCG'}<br />{'UnityVRchat,Cluster'}<br /></Poptext><br />
            <Poph2>{''}</Poph2><Poptext>{'Aviutl,,'}</Poptext><br />
            <Poph2>{''}</Poph2><Poptext>{'Canon EOS RP,'}</Poptext><br />
        </div>
    );
}

const SLink = styled(Link)`
    color: aliceblue;
`;

export default PageSkills;