import React from 'react';
import styled from "styled-components";
import { FormEventHandler } from "react";

const handleSubmit: FormEventHandler<HTMLFormElement> = async (event) => {
    event.preventDefault();
    const form = new FormData(event.currentTarget);

    const response = await fetch('/mailform', {
        method: 'POST',
        body: form, 
    });

    if (response.ok) {
        const result = await response.text();
        alert("Form submitted successfully!");
        console.log('Success:', result);
    } else {
        alert("Failed to submit form.");
        console.error('Error:', response.statusText);
    }
};

function PageToi() {
    return(
        <div className="Toi">
            <p>Toi</p>
            <Sform method="POST" action="/mailform" onSubmit={handleSubmit}>
                <Slabel>
                    :
                    <Sinput type="text" name="firstName" defaultValue="" />
                </Slabel><br /><br />
                <Slabel>
                    :
                    <Sinput type="text" name="lastName" defaultValue="" />
                </Slabel><br /><br />
                <Slabel>
                    Question:
                    <Stextarea name="question" defaultValue="" />
                </Slabel><br /><br />
                <Sinput type="submit" value="Submit" />
            </Sform>
        </div>
    );
}

const Sform = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
`;

const Slabel = styled.label`
    color: white;
    font-size: 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`;

const Sinput = styled.input`
    font-size: 30px;
    width: 400px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
`;

const Stextarea = styled.textarea`
    font-size: 30px;
    width: 400px;
    height: 200px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
`;

export default PageToi;
