import Pop from './pop';
import Poptext from './Poptext';
import Slide from './Slide';
import {  Link  } from "react-router-dom";
import styled from "styled-components";

function Home() {
    return(
        <div className="Home">
            <p>Home</p>
            <Slide />
            <h1>Portfolio</h1>
            <h2>Welcome to my Portfolio Site</h2>
            <p></p>
            <p></p><br /><br /><br /><br />
            <h1></h1>
            <Pop>{'About'}</Pop>
            <Simg className="abo" src="/images/faceb.jpg" alt=""/>
            <Poptext>{''}<br />{"20224"}<br />{",CG,PC"}<br />{""}<SLink to="/PageAbout"></SLink><br /><br /><br /><br /></Poptext>
            <Shr></Shr>
            <Pop>{'Skills'}</Pop>
            <Simg className="abo" src="/images/internetb.jpg" alt=""/>
            <Poptext>{''}<br /><br />{""}<br />{"JavaScript{React,express,Babylon},Python{Django},C,Java,HTML,CSS"}<br /><br />{""}<br />{": Studio One"}<br /><br />{"CG"}<br />{": Blender"}<br /><br />{""}<SLink to="/PageSkills"></SLink><br /><br /><br /><br /></Poptext>
            <Shr></Shr>
            <Pop>{'Works'}</Pop>
            <Simg className="abo" src="/images/gakkib.jpg" alt=""/>
            <Poptext>{''}<br />{""}<br />{""}<SLink to="/PageWorks"></SLink><br /><br /><br /><br /></Poptext>
            <Shr></Shr>
            <Pop>{'Contact'}</Pop>
            <Simg className="abo" src="/images/creatorb.jpg" alt=""/>
            <Poptext>{''}<SLink to="/PageToi"></SLink></Poptext>
        </div>
    );
}

const SLink = styled(Link)`
    color: aliceblue;
`;

const Shr = styled.hr`
    width: 80%;
`;

const Simg = styled.img`
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-position: center;
    margin: auto;
`;


export default Home;